.sectionJobOffer{
    background-color:#9933ff;
    display: flex;
    justify-content: center;
    align-items: center;
}
.mail{
    background: #ecf3f8;
    padding-top: 95px;
    padding-bottom: 100px;
}
.input{
    text-align: center;
    font-size: 15px;
}
.name {
    margin: 0 auto;
    display: block;
    margin-top: 65px;
    width: 342px;
    height: 48px;
    padding-left: 22px;
    border: 1px solid #bbbbbb;
}
.email{
    margin: 0 auto;
    display: block;
    margin-top: 15px;
    width: 342px;
    height: 48px;
    padding-left: 22px;
    border: 1px solid #bbbbbb;
}
.tel{
    margin: 0 auto;
    display: block;
    margin-top: 15px;
    width: 342px;
    height: 48px;
    padding-left: 22px;
    border: 1px solid #bbbbbb;
}


.btn{
    display: block;
    margin: 0 auto;
    margin-top: 15px;
    width: 250px;
    border-radius: 20px;
    height: 55px;
    background: #ffd429;
    padding: 20px 0;
    text-align: center;
    border: 1px solid #bbbbbb;
    text-decoration: none;
    color: black;
}


@media screen and (max-width: 576px) {
    .btn {

        margin-top: 68px;
        width: 231px;
        height: 55px;
        border-radius: 10px;
    }

    .name {
        margin-top: 65px;
        width: 256px;
        height: 48px;

    }
    .email{
        margin-top: 15px;
        width: 255px;
        height: 48px;
        padding-left: 22px;
    }
    .tel{
        margin-top: 15px;
        width: 256px;
        height: 48px;
        padding-left: 22px;

    }
}