}
.btn{
    display: block;
    margin: 0 auto;
    margin-top: 15px;
    width: 342px;
    height: 55px;
    background: #ffd429;
    padding: 20px 0;
    text-align: center;
    border: 1px solid #bbbbbb;
    text-decoration: none;
    color: black;
    border-radius: 20px;
}
