.footer{
    background-color:#999;
    display: flex;
    justify-content: center;
    align-items: center;

}
.footer_socials{
    display: flex;
   /* width: 70%;
    height: 55%;*/
    border: 1px solid;
    justify-content: space-between;
    background-color:#ff99ff;
}
.social{
    height: 100px;
    width: 100px;
    border: 1px solid;
    background-color: #999;
    margin: 10px;
}

@media screen and (max-width: 576px) {
.social{
    width: 50px;
    height: 50px;
}
}