.header{
    display: flex;
    background-color: #ff9999;
    height: 70px;
    justify-content: center;
}

.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 350px;
}
@media screen and (max-width: 576px) {

    .header {
        height: 70px;
        justify-content: center;
    }
    .nav {
        width: 265px;
        margin: 0 auto;
    }
}
